<template>
  <div>
    <header-view/>
      <div class="top">
        <h2 class="big-title" style="font-size:60px;"></h2>
        <p class="" style="font-size: 32px;font-weight: 100; margin-top:20px;">VC</p>     
        <div class="flex-space-between limited-width" style="margin-top:60px; align-items:flex-start;">
          <div class="info">
            <div style="align-items:flex-start; margin-bottom:10px;">
              <span class="big-title">15</span>
              <span class="desc"></span>
            </div>
            <div class="desc">DCM15</div>
          </div>
          <div class="info">
            <div style="align-items:flex-start; margin-bottom:10px;">
              <span class="big-title">36</span>
              <span class="desc"></span>
            </div>
            <div class="desc">HiCTO36Hitales50%</div>
          </div>
          <div class="info">
            <div style="align-items:flex-start; margin-bottom:10px;">
              <span class="big-title">65</span>
              <span class="desc">%</span>
            </div>
            <div class="desc">65%IDG</div>
          </div>
        </div> 
      </div>
      <div class="limited-width">
        <section>
          <div class="flex-space-between">
            <div class="value">
              <div></div>
              <div></div>
            </div>
            <div class="value">
              <div></div>
              <div> & </div>
            </div>
            <div class="value">
              <div></div>
              <div></div>
            </div>
          </div>
        </section>
        <div id="team" class="seperator-line"></div>
        <section >
          <div class="big-title centered-text" style="margin-bottom:30px;"></div>
          <div class="flex-space-between" style="align-items:flex-start; flex-wrap: wrap;">
            <div class="member" v-for="member in data.members">
              <img class="avatar centered" :src="member.avatar" alt="">
              <div class="name">{{member.name}}</div>
              <div class="position">{{member.position}}</div>
              <div class="intro">
                <div v-for="title in member.titles">{{title}}</div>
              </div>
              <div class="intro">
                <div  v-for="intro in member.introduction">{{intro}}</div>
              </div>
            </div>
          </div>
          <div class="centered" style="font-size: 20px; font-weight:300; max-width:650px; line-height:1.5;">
            HiCTO EMCSAP  
          </div>
        </section>
        <div id="projects"class="seperator-line"></div>
        <section >
          <div class="big-title centered-text" style="margin-bottom:30px;"></div>
          <div class="centered" style="font-size: 20px; font-weight:300; max-width:650px; line-height:1.5;">
            HiCTOHitalesNondaCarblock65%
          </div>
          <div class="flex-space-between" style="flex-wrap: wrap; margin-top: 80px;">
            <div class="client" v-for="client in data.clients">
              <img class="icon centered" :src="client.icon" alt=""/>
              <div class="centered-text" style="font-size:18px; font-weight:bold;margin-top:20px;">{{client.name}}</div>
            </div>
          </div>
        </section>
        <div id="funds" class="seperator-line"></div>
        <section >
          <div class="big-title centered-text" style="margin-bottom:30px;"></div>
          <div class="flex-space-between" style="flex-wrap: wrap;">
            <div class="vc" v-for="vc in data.vcs">
              <img class="centered" :src="vc.icon" alt=""/>
            </div>
          </div>
        </section>
      </div>
    <footer-view/>
  </div>
</template>

<script>
import HeaderView from "@/components/HeaderView";
import FooterView from "@/components/FooterView";
import data from "@/libs/indexData";

export default {
  components: { HeaderView, FooterView },
  data() {
    return {
      data: data
    };
  },
  methods: {
    showForm() {
      ysf("open");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.top {
    margin-top: -80px;
    padding: 260px 20px 120px 20px;
    box-sizing: border-box;
    background: url(~@/assets/bg.jpg) no-repeat center;
    background-size: cover;
    color: #ffffff;
    position: relative;
    text-align: center;
}

.top .info {
  width: 24%;
  margin-bottom: 30px;
}

.top .desc {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.8;
}

section {
  padding: 70px 0px;
}

.value {
  text-align: center;
  width:33%;
  font-size: 30px;
  font-weight: 100;
}

.member {
  text-align: center;
  width:30%;
  margin-bottom: 80px;
}

.member .avatar {
  border-radius: 50%;
  height: 210px;
  width: 210px;
}
.seperator-line {
  margin-bottom:30px;
}
.member .name {
  font-size: 28px;
  font-weight: bold;
  margin: 20px 0px 10px 0px;
}
.member .position {
  color: #999;
}
.member .intro {
  color: #666;
  line-height: 1.6;
  margin: 10px 0px 10px 0px;
}

.client {
  width: 25%;
  margin-bottom: 80px;
}
.client img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.vc {
  width: 25%
}
.vc img {
  width: 80%;
}
/* mobile */
@media screen and (max-width: 700px) {
  .top .info {
    width: 100%;
  }
  .value {
    margin-bottom: 30px;
  }
  .flex-space-between {
    display: block;
  }
  .flex-space-between div {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
  }
}
</style>



